import React from 'react';
import {Link} from "react-router-dom";

export default class Tattoo extends React.Component{
	constructor(props){
		super(props);
		this.state={};
	}	
	render(){
		return(
			<Link to={'/works/'+this.props.id} className='tattoo anime'>
				<div className='img'>
					<img src={require('../../img/db/'+this.props.img[0])}/>
				</div>
				<div className='master'>{this.props.master}</div>
			</Link>
		)
	}
}