import React from 'react';
import Tattoo from './Tattoo.jsx';
import {catalog} from '../db.js';

export default class Works extends React.Component{
	constructor(props){
		super(props);
		this.state={};
	}
	render(){
		return(
			<section className='works'>
				{
					catalog.map((i,n) =>{
						return <Tattoo key={n} id={i.id} img={i.photo} master={i.master}/>
					})
				}
			</section>
		)
	}
}